import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useAsyncFn, useDeepCompareEffect } from 'react-use';
import { AnimatePresence } from 'framer-motion';
import { FiCheck, FiX } from 'react-icons/fi';

import { cls } from '@mssgme/helpers';
import {
    __t,
    SubscriptionFormBlockKind,
    mapDynamicInputsToValidationSchema,
    mapDynamicFieldToName,
    defaultDynamicFieldLengthLimit,
} from '@mssgme/shared';
import { feedbackApi } from '@mssgme/lib';
import { FormikProvider, LandingsPropTypes, SubmitButton, useEnhancedFormik } from '@mssgme/ui';

import { useBlockTheme, useWebsiteData } from '../../../hooks';
import { ThemedButton, ThemedModal, DynamicFormInputs } from '../../../UI';
import { BlockBase } from '../BlockBase';

import styles from './SubscriptionForm.scss';

export const SubscriptionFormContent = ({ block, theme, onSubmit: rawSubmit, interactive }) => {
    const [sent, setSent] = useState(null);
    const validationSchema = useMemo(() => mapDynamicInputsToValidationSchema(block), [block]);
    const FormTag = interactive ? 'form' : 'div';

    const [{ loading, error }, onSubmit] = useAsyncFn(
        async (values) => {
            if (false === (await rawSubmit(values))) {
                return;
            }

            const items = block.items.map((item, index) => {
                const field = mapDynamicFieldToName(item, index);
                const value = values[item._id] ?? values[field];

                return {
                    key: field,
                    label: item.label,
                    value,
                };
            });
            const pairs = items.filter(({ value }) => value);

            setSent(pairs);
        },
        [rawSubmit]
    );

    const errors = Object.entries(error?.errors || {}).reduce((acc, [key, value]) => {
        acc[key] = __t(value);

        return acc;
    }, {});

    const formik = useEnhancedFormik({
        initialValues: useMemo(() =>
            block.items.reduce((acc, item, index) => ((acc[mapDynamicFieldToName(item, index)] = ''), acc), {})
        ),
        validationSchema,
        onSubmit,
    });

    const handleSentModalClosed = useCallback(() => {
        setSent(false);
        formik.resetForm();
    }, [formik]);

    useEffect(() => {
        if (sent && block.hasUrl && block.sentUrl) {
            const timer = setTimeout(() => (window.location = block.sentUrl), 1000);

            return () => clearTimeout(timer);
        }
    }, [sent, block.hasUrl, block.sentUrl, formik]);

    useDeepCompareEffect(() => {
        formik.setErrors(errors);
    }, [errors]);

    return (
        <FormikProvider value={formik}>
            <FormTag id={`form_${block._id}`}>
                <DynamicFormInputs theme={theme} block={block} defaultMaxSize={defaultDynamicFieldLengthLimit} />

                <div className={styles.buttonContainer}>
                    <SubmitButton
                        as={ThemedButton}
                        theme={theme}
                        isInitialHidden={false}
                        fullWidth
                        isFetching={loading}
                        disabled={false}
                        style={{
                            backgroundColor: theme.buttonBackground,
                            color: theme.buttonTextColor,
                            borderRadius: theme.borderRadius,
                        }}
                    >
                        {block.sendLabel}
                    </SubmitButton>
                </div>
            </FormTag>

            <AnimatePresence>
                {sent && (
                    <ThemedModal compact overlay showHeader={false} onClose={handleSentModalClosed}>
                        {(handleClose) => (
                            <div className={styles.successContent}>
                                <div className={styles.closeIcon} onClick={handleClose}>
                                    <FiX size={26} />
                                </div>
                                <FiCheck size={60} className={styles.icon} />
                                <p>{block.sentText}</p>
                            </div>
                        )}
                    </ThemedModal>
                )}
            </AnimatePresence>
        </FormikProvider>
    );
};

SubscriptionFormContent.propTypes = {
    block: LandingsPropTypes.block.isRequired,
    theme: PropTypes.object.isRequired,
    onSubmit: PropTypes.func.isRequired,
    interactive: PropTypes.bool,
};

export default function SubscriptionForm({ block, style: baseStyle, interactive, ...rest }) {
    const { theme, style } = useBlockTheme(block);
    const website = useWebsiteData();
    const combined = useMemo(() => (baseStyle ? { ...style, ...baseStyle } : style), [style, baseStyle]);

    const handleSubmit = useCallback(
        async (values) => {
            if (!interactive) {
                return;
            }

            await feedbackApi.submit(website.workspace, website._id, { blockId: block._id, data: values });

            const submitEvent = new CustomEvent('formSubmitted', {
                detail: block._id,
            });

            document.dispatchEvent(submitEvent);
        },
        [website, interactive]
    );

    return (
        <BlockBase interactive={interactive} style={combined} {...cls(styles.root, rest)}>
            <SubscriptionFormContent block={block} theme={theme} interactive={interactive} onSubmit={handleSubmit} />
        </BlockBase>
    );
}

SubscriptionForm.propTypes = {
    ...BlockBase.propTypes,
    block: LandingsPropTypes.block.isRequired,
};

SubscriptionForm.defaultProps = BlockBase.defaultProps;

SubscriptionForm.kind = SubscriptionFormBlockKind;
